import AppStyle from './components/AppStyle.vue'
import GridStyle from './components/Styling/GridStyle.vue';

export default {
  name: 'App',
  components: {
    NavDrawer: () => import('./components/General/NavDrawer.vue'),
    NewMenu: () => import('./components/General/NewMenu.vue')
  },
  data: () => ({
    companyName: 'Company Name',
    drawer: true
  }),
  methods:{
    ToggleDrawer(){
      this.drawer = !this.drawer
      this.$nextTick(() => {
        this.$refs.NavDrawer.ToggleDrawer(this.drawer)
      })      
    },
    DrawerChanged(e){
      this.drawer = e
    }
  }
};
